body {
    background: url(use_your_illusion.png);
    color: #eee;
    font: 1em 'PT Sans', sans-serif;
    }
  * {
    box-sizing: border-box;
  }
  .form{
      font-family: 'Times New Roman', Times, serif;
      margin-top: 5px;
      margin-right: 10px;
      margin-left: 10px;
      width: 160px;
      font-size: 13px;
      padding: 6px 0 4px 10px;
      border: 1px solid #cecece;
      background: #F6F6f6;
      border-radius: 7px;
  }
  .select{
      font-family: 'Times New Roman', Times, serif;
      margin-right: 10px;
      width: 100px;
      font-size: 13px;
      padding: 6px 0 4px 10px;
      border: 1px solid #cecece;
      background: #F6F6f6;
      border-radius: 7px;
  }
  .button{
      font-family: 'Times New Roman', Times, serif;
      width: 65px;
      font-size: 13px;
      margin-left: 10px;
      padding: 6px 0 4px 0px;
      border: 1px solid #000000;
      background: rgb(230, 127, 10);
      border-radius: 7px;
  }
  
  .tabbed {
    width: 750px;
    margin: 60px auto;
  }
  
  .tabbed > input {
    display: none;
  }
  
  .tabbed > label {
    color: white;
    display: block;
    float: left;
    padding: 12px 20px;
    margin-right: 5px;
    cursor: pointer;
    transition: background-color .3s;
  }
  
  .tabbed > label:hover,
  .tabbed > input:checked + label {
    background: #EC7B19;
    color: black;
  }
  
  .tabs {
    clear: both;
    perspective: 600px;
  }
  
  .tabs > div {
    width: 750px;
    position: absolute;
    border: 2px solid #EC7B19;
    padding: 10px 30px 40px;
    line-height: 1.4em;
    opacity: 0;
    transform: rotateX(-90deg);
    transform-origin: top center;
    transition: opacity .3s, transform 1s;
    z-index: 0;
  }
  
  #tab-nav-1:checked ~ .tabs > div:nth-of-type(1),
  #tab-nav-2:checked ~ .tabs > div:nth-of-type(2),
  #tab-nav-3:checked ~ .tabs > div:nth-of-type(3) {
    transform: rotateX(0);
    opacity: 1;
    z-index: 1;
  }
  
  @media screen and (max-width: 750px) {
    .tabbed {
      width: 400px;
    }
  
    .tabbed > label {
      display: none;
    }
  
    .tabs > div {
      width: 400px;
      border: none;
      padding: 0;
      opacity: 1;
      position: relative;
      transform: none;
      margin-bottom: 60px;
    }
  
    .tabs > div h2 {
      border-bottom: 2px solid #EC7B19;
      padding-bottom: .5em;
    }
    #tab-nav-1:checked ~ .tabs > div:nth-of-type(1),
    #tab-nav-2:checked ~ .tabs > div:nth-of-type(2),
    #tab-nav-3:checked ~ .tabs > div:nth-of-type(3) {
      transform: rotateX(0);
      opacity: 1;
      z-index: 1;
  }
  }
  